// pages/order.js (or wherever your OrderPage is located)
import { useState, useEffect } from 'react';
import '../order/order.css'; // Adjust path if necessary (e.g., styles/order.css)

export default function OrderPage() {
  interface Order {
    _id: string;
    user: {
      username: string;
    };
    createdAt: string;
    status: string;
  }

  const [orders, setOrders] = useState<Order[]>([]);
  const [error, setError] = useState<string | null>(null);

  // Fetch orders from the API
  useEffect(() => {
    const fetchOrders = async () => {
      try {
        const response = await fetch('https://api-zeal.onrender.com/api/orders/all');
        if (!response.ok) throw new Error('Failed to fetch orders');
        const data = await response.json();
        setOrders(data);
      } catch (error) {
        console.error('Error fetching orders:', error);
        setError('Không thể tải danh sách đơn hàng');
      }
    };
    fetchOrders();
  }, []);

  // Format date to DD-MM-YYYY
  const formatDate = (dateString: string | number | Date) => {
    const date = new Date(dateString);
    return `${date.getDate().toString().padStart(2, '0')}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getFullYear()}`;
  };

  if (error) return <div>{error}</div>;
  if (!orders.length) return <div>Đang tải...</div>;

  return (
    <div className="order-container">
      <div className="title">
        <h1>Danh Sách Order</h1>
      </div>
      <div className="table-container">
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th>Tên</th>
              <th>Địa Chỉ</th>
              <th>Ngày</th>
              <th>Status</th>
            </tr>
          </thead>
          <tbody>
            {orders.map((order, index) => (
              <tr key={order._id}>
                <td>
                  <a href={`/orderdetail/${order._id}`}>
                    {index + 1}
                  </a>
                </td>
                <td>
                  <a href={`/orderdetail/${order._id}`}>
                    {order.user.username}
                  </a>
                </td>
                <td>
                  <a href={`/orderdetail/${order._id}`}>
                    {/* Placeholder: API does not provide address */}
                    Unknown
                  </a>
                </td>
                <td>
                  <a href={`/orderdetail/${order._id}`}>
                    {formatDate(order.createdAt)}
                  </a>
                </td>
                <td>
                  <div
                    className={
                      order.status === 'shipped' || order.status === 'pending'
                        ? 'table-status-fail'
                        : order.status === 'cancelled'
                        ? 'table-status-cancelled'
                        : 'table-status-success'
                    }
                  >
                    {order.status.charAt(0).toUpperCase() + order.status.slice(1)}
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}